// Medium devices (tablets, 768px and up)
@media only screen and (max-width : 767px){
	#wrapper {
//		padding: 0 !important;
	}
	
	div.searchbox{
		margin: auto;
	}
	.searchbox {
		margin: auto !important;
	}
