#slideContainer{
  height:auto;
}

#slideContainer #rightControl , #slideContainer #pauseControl, #slideContainer #playControl, #slideContainer #leftControl{
  top:0px;
}

#slideContainer { width:330px; height:140px; overflow:auto; position: relative;}
#slideContainer .slide { margin:0 auto; width:330px; height:140px; }
#slideContainer .control { display:block; width:50px; height:auto; text-indent:-10000px; position:absolute; cursor: pointer; }
#slideContainer #leftControl {top:0px; right:50px; background:transparent url("icoleft.gif") no-repeat 0 0; }
#slideContainer #playControl {top:0px; right:25px; background:transparent url("icoplay.gif") no-repeat 0 0; display: none; }
#slideContainer #pauseControl { top:0px; right:25px; background:transparent url("icopause.gif") no-repeat 0 0; }
#slideContainer #rightControl { top:0px; right:0px; background:transparent url("icoright.gif") no-repeat 0 0; }






